.Root,
body,
html {
    height: 100%;
    width: 100%;
    color: #eee;
}

a {
    color: #eee;
    text-decoration: none;
}
a:link {
    color: #eee;
    text-decoration: none;
}
a:visited {
    color: #eee;
    text-decoration: none;
}
a:hover {
    color: #fff;
    text-decoration: none;
}
a:active {
    color: #fff;
    text-decoration: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
